// components/ContactForm.js

import React from 'react';
import { Box, Container, Grid, Typography, TextField, Button } from '@mui/material';

const ContactForm = () => {
  return (
    <Box sx={{ py: 6, bgcolor: 'grey.100' }}>
      <Container>
        <Typography variant="h4" gutterBottom>
          Контакты
        </Typography>
        <Grid container spacing={4}>
          <Grid item xs={12} md={6}>
            <Typography variant="h6">Телефон</Typography>
            <Typography variant="body1" gutterBottom>
              +7 (8452) 39-99-74
            </Typography>

            <Typography variant="h6">E-mail</Typography>
            <Typography variant="body1" gutterBottom>
              info@arkudast.ru
            </Typography>

            <Typography variant="h6">Часы работы</Typography>
            <Typography variant="body1" gutterBottom>
              Пн-Пт: с 8:00 до 18:00
            </Typography>

            <Typography variant="h6">Руководитель отдела продаж</Typography>
            <Typography variant="body1">
              Насонов Артём, +7 (927) 143-77-72
            </Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="h6" gutterBottom>
              Обратная связь
            </Typography>
            <form>
              <TextField
                fullWidth
                label="Имя"
                placeholder="Укажите, пожалуйста, ваше имя"
                variant="outlined"
                margin="normal"
                required
              />
              <TextField
                fullWidth
                label="Телефон"
                placeholder="+7 (495) 000-00-00"
                variant="outlined"
                margin="normal"
                required
              />
              <TextField
                fullWidth
                label="Комментарий"
                multiline
                rows={4}
                variant="outlined"
                margin="normal"
              />
              <Button variant="contained" color="primary" sx={{ mt: 2 }}>
                Отправить
              </Button>
            </form>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default ContactForm;
